"use client";
import { useState, useEffect } from "react";
import { getAuth } from "firebase/auth";
import { getAnalytics } from "firebase/analytics";
import { app, db } from "@/app/api/firebase";
import { collection, doc, getDoc, getDocs } from "firebase/firestore";

import Sidebar from "@/app/components/sidebar";
import Navbar from "@/app/components/navbar";
import ProjectCard from "@/app/components/ProjectCardforHS";
import MeetingCard from "@/app/components/MeetingCard";

export default function Home() {
  const auth = getAuth(app);
  const analytics = getAnalytics(app);

  // üîπ ID —Ç–µ–∫—É—â–µ–π –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏ (–ø–æ–∫–∞ –∂—ë—Å—Ç–∫–æ, –ø–æ—Ç–æ–º –º–æ–∂–Ω–æ –ø—Ä–∏–≤—è–∑–∞—Ç—å –∫ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é)
  const orgId = "GeoLab"; // <-- –∑–∞–º–µ–Ω–∏—à—å –Ω–∞ –¥–∏–Ω–∞–º–∏—á–µ—Å–∫–∏–π orgId, –µ—Å–ª–∏ –Ω—É–∂–Ω–æ

  const [organization, setOrganization] = useState(null);
  const [users, setUsers] = useState({});
  const [projects, setProjects] = useState([]);
  const [meetings, setMeetings] = useState([]);
  const [loading, setLoading] = useState(true);

  // === –ü–æ–ª—É—á–∞–µ–º –¥–∞–Ω–Ω—ã–µ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏ ===
  const fetchOrganization = async () => {
    try {
      const orgRef = doc(db, "organizations", orgId);
      const orgSnap = await getDoc(orgRef);
      if (orgSnap.exists()) {
        setOrganization({ id: orgSnap.id, ...orgSnap.data() });
      } else {
        console.warn("Organization not found");
      }
    } catch (err) {
      console.error("Error fetching organization:", err);
    }
  };

  // === –ó–∞–≥—Ä—É–∂–∞–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π ===
  const fetchUsers = async () => {
    try {
      const usersSnapshot = await getDocs(collection(db, `organizations.${orgId}users`));
      const usersMap = {};
      usersSnapshot.forEach((doc) => {
        usersMap[doc.id] = doc.data().name || doc.data().email;
      });
      setUsers(usersMap);
      return usersMap;
    } catch (error) {
      console.error("Error fetching users:", error);
      return {};
    }
  };

  // === –ó–∞–≥—Ä—É–∂–∞–µ–º –ø—Ä–æ–µ–∫—Ç—ã –∏ –º–∏—Ç–∏–Ω–≥–∏ ===
  const fetchData = async () => {
    try {
      const usersMap = await fetchUsers();

      // Projects
      const projectsSnapshot = await getDocs(collection(db, "organizations", orgId, "projects"));
      const projectsList = projectsSnapshot.docs.map((doc) => {
        const data = doc.data();
        return {
          id: doc.id,
          ...data,
          createdByName: usersMap[data.createdBy] || data.createdBy,
          membersNames: data.members?.map((id) => usersMap[id] || id) || [],
        };
      });
      setProjects(projectsList);

      // Meetings
      const meetingsSnapshot = await getDocs(collection(db, "organizations", orgId, "meetings"));
      const meetingsList = meetingsSnapshot.docs.map((doc) => {
        const data = doc.data();
        return {
          id: doc.id,
          ...data,
          participantsNames: data.participants?.map((id) => usersMap[id] || id) || [],
        };
      });
      setMeetings(meetingsList);
    } catch (error) {
      console.error("Error fetching data:", error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    (async () => {
      await fetchOrganization();
      await fetchData();
    })();
  }, []);

  const today = new Date().toLocaleDateString("en-GB", {
    day: "2-digit",
    month: "2-digit",
    year: "numeric",
    weekday: "long",
  });

  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen">
        <p>Loading data...</p>
      </div>
    );
  }

  return (
    <div className="flex">
      <Sidebar />
      <div className="flex-1">
        <Navbar />
        <div className="p-6">
          {/* === –û—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏—è === */}
          <div className="bg-gray-50 rounded-xl p-6 mb-6 shadow">
            <h1 className="text-3xl font-bold mb-2">{organization?.name || "Organization"}</h1>
            <p className="text-gray-600">
              Created:{" "}
              {organization?.createdAt
                ? new Date(organization.createdAt.toDate()).toLocaleDateString("en-GB")
                : "N/A"}
            </p>
            {organization?.settings && (
              <pre className="bg-gray-100 rounded-lg p-3 mt-2 text-sm text-gray-700">
                {JSON.stringify(organization.settings, null, 2)}
              </pre>
            )}
          </div>

          {/* === –ü—Ä–∏–≤–µ—Ç—Å—Ç–≤–∏–µ === */}
          <div className="bg-gray-100 rounded-xl p-6 mb-6">
            <h1 className="text-2xl font-bold mb-2">
              Hello {users[auth.currentUser?.uid] || "User"}
            </h1>
            <div className="text-gray-600">
              <h2 className="text-lg">Today</h2>
              <p className="text-xl font-semibold">{today}</p>
            </div>
          </div>

          {/* === –ü—Ä–æ–µ–∫—Ç—ã === */}
          <div className="mb-8">
            <h2 className="text-xl font-semibold mb-4">Your Projects</h2>
            <div className="overflow-x-auto">
              <div className="flex gap-4 pb-2">
                {projects.length > 0 ? (
                  projects.map((project) => (
                    <div key={project.id} className="min-w-[400px] max-w-[400px]">
                      <ProjectCard project={project} />
                    </div>
                  ))
                ) : (
                  <p className="text-gray-500">No projects yet.</p>
                )}
              </div>
            </div>
          </div>

          {/* === –ú–∏—Ç–∏–Ω–≥–∏ === */}
          <div className="bg-blue-50 rounded-xl p-6">
            <h2 className="text-xl font-semibold mb-4">Meetings</h2>
            <div className="overflow-x-auto">
              <div className="flex gap-4 pb-2">
                {meetings.length > 0 ? (
                  meetings.map((meeting) => (
                    <div key={meeting.id} className="min-w-[300px] max-w-[300px]">
                      <MeetingCard meeting={meeting} />
                    </div>
                  ))
                ) : (
                  <p className="text-gray-500">No meetings yet.</p>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
